import React, { useState, useRef, useEffect } from 'react'

import { useNavigate } from 'react-router-dom'
import { Link } from 'react-router-dom'
import {
    AcademicCapIcon,
    Cog6ToothIcon,
    WrenchIcon,
} from '@heroicons/react/24/outline'

import logo from '../../../../images/logo.png'

import MenuTextItem from './components/MenuTextItem'
import MenuIconItem from './components/MenuIconItem'
import SubMenuItem from './components/SubMenuItem'

import { TextLink } from './types'
import './NavBar.css'
import { useAuth } from '../../../../hooks/auth'

const MENU_KEY_ORG = 'org'
const MENU_KEY_USER = 'user'

const SUB_MENU_ITEMS = new Map<string, TextLink[]>([
    [
        MENU_KEY_ORG,
        [
            { link: 'org/home', text: 'Организация' },
            { link: 'org/users', text: 'Пользователи' },
            { link: 'org/structure', text: 'Структура' },
            { link: 'org/roles', text: 'Роли' },
            { link: 'org/methods', text: 'Способы закупки' },
            { link: 'org/lists', text: 'Перечни' },
            { link: 'org/handbook', text: 'Справочники' },
        ],
    ],
    [
        MENU_KEY_USER,
        [
            { link: 'home', text: 'Главная страница' },
            { link: 'plans', text: 'План закупки' },
        ],
    ],
])

const NavBar: React.FC = () => {
    const initialState = window.location.hash.startsWith('#/hub/org')
        ? MENU_KEY_ORG
        : MENU_KEY_USER
    const [currentMenuItem, setCurrentMenuItem] = useState(initialState)
    const { user, logout } = useAuth()
    const [isOpen, setIsOpen] = useState(false)
    const dropdownRef = useRef<HTMLDivElement>(null)
    const navigate = useNavigate()

    const handleLogout = () => {
        fetch('https://itzakupki.ru/api/auth/v1/users/logout/', {
            method: 'POST',
            credentials: 'include',
            headers: {
                'Content-Type': 'application/json',
            },
        }).then((r) => {
            if (r.ok) {
                logout()
            }
        })
    }

    const toggleDropdown = () => {
        setIsOpen(!isOpen)
    }

    const handleClickOutside = (event: MouseEvent) => {
        if (
            dropdownRef.current &&
            !dropdownRef.current.contains(event.target as Node)
        ) {
            setIsOpen(false)
        }
    }

    useEffect(() => {
        if (isOpen) {
            document.addEventListener('mousedown', handleClickOutside)
        } else {
            document.removeEventListener('mousedown', handleClickOutside)
        }

        return () => {
            document.removeEventListener('mousedown', handleClickOutside)
        }
    }, [isOpen])

    return (
        <>
            <div className="h-full flex flex-col p-1 pb-2.5 items-center bg-[#181818]">
                <img src={logo} alt="Логотип" className="w-10 h-10 mb-4" />
                <ul className="flex flex-col gap-y-1 grow items-center">
                    <MenuTextItem
                        text="Орг"
                        isActive={currentMenuItem === MENU_KEY_ORG}
                        onClick={() => {
                            setCurrentMenuItem(MENU_KEY_ORG)
                        }}
                    />
                    <li className="divider" />
                    <MenuTextItem
                        text="223"
                        isActive={currentMenuItem === MENU_KEY_USER}
                        onClick={() => {
                            setCurrentMenuItem(MENU_KEY_USER)
                        }}
                    />
                    <li className="divider" />
                    <MenuIconItem
                        link=""
                        icon={<AcademicCapIcon className="w-4 h-4" />}
                    />
                    <MenuIconItem
                        link=""
                        icon={<WrenchIcon className="w-4 h-4" />}
                    />
                </ul>
                {/* <div className="flex w-10 h-10 rounded-full items-center justify-center bg-amber-700 text-sm text-white">
                    ИИ
                </div> */}
            </div>
            <div className="h-full flex flex-col min-w-36 p-2 bg-[#262427]">
                <div className="mt-1 mb-6 text-white uppercase leading-none">
                    PLAN223.ru
                </div>
                <ul className="flex flex-col grap-y-1 grow">
                    {(SUB_MENU_ITEMS.get(currentMenuItem) as TextLink[]).map(
                        (item, index) => (
                            <SubMenuItem
                                key={index}
                                link={item.link}
                                text={item.text}
                            />
                        )
                    )}
                </ul>
                <div
                    className="relative inline-block text-left"
                    ref={dropdownRef}
                >
                    <div
                        className="flex flex-row h-10 items-center p-2.5 rounded text-xs text-[#8A8A8A] hover:bg-[#464447] hover:text-white cursor-pointer"
                        onClick={toggleDropdown}
                    >
                        <div className="grow items-center justify-center text-xs">
                            {user?.object.login.split('@')[0]}
                        </div>{' '}
                        <Cog6ToothIcon className="h-5 w-5" />
                    </div>
                    {isOpen && (
                        <div
                            className="absolute flex flex-col bg-[#2A282B] w-[150px] shadow-lg rounded-lg p-1 z-50 text-[12.8px] items-center justify-start gap-1"
                            style={{ bottom: '100%' }}
                        >
                            <div className="flex w-full hover:bg-[#464447] text-[#8A8A8A] hover:text-white items-center rounded">
                                <button
                                    className="flex flex-1 flex-row py-3 pl-1 w-full text-xs text-left  h-full gap-2"
                                    onClick={() => {
                                        navigate(
                                            `/hub/org/users/${user?.object.user_id}`
                                        )
                                        setIsOpen(!isOpen)
                                    }}
                                >
                                    <svg
                                        className="w-4 h-4"
                                        xmlns="http://www.w3.org/2000/svg"
                                        data-name="Layer 1"
                                        viewBox="0 0 24 24"
                                        id="user-circle"
                                    >
                                        <path
                                            fill="#FFFFFF"
                                            d="M12,2A10,10,0,0,0,4.65,18.76h0a10,10,0,0,0,14.7,0h0A10,10,0,0,0,12,2Zm0,18a8,8,0,0,1-5.55-2.25,6,6,0,0,1,11.1,0A8,8,0,0,1,12,20ZM10,10a2,2,0,1,1,2,2A2,2,0,0,1,10,10Zm8.91,6A8,8,0,0,0,15,12.62a4,4,0,1,0-6,0A8,8,0,0,0,5.09,16,7.92,7.92,0,0,1,4,12a8,8,0,0,1,16,0A7.92,7.92,0,0,1,18.91,16Z"
                                        ></path>
                                    </svg>
                                    Учетная запись
                                </button>
                            </div>
                            <div className="flex w-full hover:bg-[#464447] text-[#8A8A8A] hover:text-white  items-center rounded">
                                <button
                                    className="flex flex-1 flex-row py-3 pl-1 w-full text-xs text-left h-full gap-2"
                                    onClick={handleLogout}
                                >
                                    <svg
                                        className="w-4 h-4"
                                        xmlns="http://www.w3.org/2000/svg"
                                        data-name="Layer 1"
                                        viewBox="0 0 24 24"
                                        id="user-circle"
                                    >
                                        <path
                                            fill="#FFFFFF"
                                            d="M4,12a1,1,0,0,0,1,1h7.59l-2.3,2.29a1,1,0,0,0,0,1.42,1,1,0,0,0,1.42,0l4-4a1,1,0,0,0,.21-.33,1,1,0,0,0,0-.76,1,1,0,0,0-.21-.33l-4-4a1,1,0,1,0-1.42,1.42L12.59,11H5A1,1,0,0,0,4,12ZM17,2H7A3,3,0,0,0,4,5V8A1,1,0,0,0,6,8V5A1,1,0,0,1,7,4H17a1,1,0,0,1,1,1V19a1,1,0,0,1-1,1H7a1,1,0,0,1-1-1V16a1,1,0,0,0-2,0v3a3,3,0,0,0,3,3H17a3,3,0,0,0,3-3V5A3,3,0,0,0,17,2Z"
                                        ></path>
                                    </svg>
                                    Выход
                                </button>
                            </div>
                        </div>
                    )}
                </div>
                {/* <Link
                    to={`/hub/org/users/${user?.object.user_id}`}
                    title={user?.object.login.split('@')[0]}
                    className="flex flex-row h-10 items-center p-2.5 rounded text-xs text-zinc-500 hover:bg-[#464447] hover:text-white"
                >
                    <div className="grow">
                        {user?.object.login.split('@')[0]}
                    </div>{' '}
                    <Cog6ToothIcon className="h-5 w-5" />
                </Link> */}
            </div>
        </>
    )
}

export default NavBar

