const getCurrentDate = (): string => {
    const today = new Date()
    const year = today.getFullYear()
    const month = String(today.getMonth() + 1).padStart(2, '0') // добавляем 1, так как месяцы начинаются с 0
    const day = String(today.getDate()).padStart(2, '0')
    return `${year}-${month}-${day}`
}

function formatDateToYYYYMMDD(isoDate: string): string {
    const date = new Date(isoDate)
    const year = date.getUTCFullYear()
    const month = String(date.getUTCMonth() + 1).padStart(2, '0')
    const day = String(date.getUTCDate()).padStart(2, '0')
    return `${year}-${month}-${day}`
}

export const loadUser = async ({ params }: any): Promise<User> => {
    if (params.guid === 'new') {
        // Возвращаем пустого пользователя для нового
        return {
            guid: null,
            first_name: '',
            last_name: '',
            patronymic: '',
            position: '',
            struct_guid: '',
            role_guid: '',
            email: '',
            phone: '',
            extension_phone: '',
            fax: null,
            birth_day: '',
            gender: '',
            organization_guid: null,
            sys_created_at: null,
            sys_updated_at: null,
        }
    }
    try {
        // Подставляем guid пользователя в URL
        const response = await fetch(
            `https://itzakupki.ru/api/backend/v1/organization/users/${params.guid}`,
            {
                method: 'GET',
                credentials: 'include',
                headers: {
                    'Content-Type': 'application/json',
                },
            }
        )

        if (!response.ok) {
            throw new Error(`Error loading user: ${response.status}`)
        }

        // Парсим JSON-ответ
        const user: User = await response.json()
        return user
    } catch (error) {
        console.error('Failed to load user:', error)
        throw error // Или обработайте ошибку соответствующим образом
    }
}

const loadStructure = async (): Promise<Structure[]> => {
    return fetch('https://itzakupki.ru/api/backend/v1/organization_struct', {
        method: 'GET',
        credentials: 'include',
        headers: {
            'Content-Type': 'application/json',
        },
    })
        .then((response) => {
            if (response.ok) {
                return response.json()
            } else {
                throw new Error('Failed to fetch organization_struct')
            }
        })
        .catch((e) => {
            console.error(e)
        })
}

const loadRole = async (): Promise<Role> => {
    return fetch('https://itzakupki.ru/api/backend/v1/organizations/roles/', {
        method: 'GET',
        credentials: 'include',
        headers: {
            'Content-Type': 'application/json',
        },
    })
        .then((response) => {
            if (response.ok) {
                return response.json()
            } else {
                throw new Error('Failed to fetch organization')
            }
        })
        .catch((e) => {
            console.error(e)
        })
}

