import { User } from './types'
import { Structure } from '../Structure/types'
import { Role } from '../Roles/types'
import './style.css'

type ApiResponse = {
    message: User[]
}

export const loadUsers = async (): Promise<User[]> => {
    return fetch('https://itzakupki.ru/api/backend/v1/organization/users', {
        method: 'GET',
        credentials: 'include',
        headers: {
            'Content-Type': 'application/json',
        },
    })
        .then((response) => {
            if (response.ok) {
                return response.json()
            } else {
                throw new Error('Failed to fetch organization')
            }
        })
        .then((response: ApiResponse) => response.message)
        .catch((e) => {
            console.error(e)
            return []
        })
}

const loadStructure = async (): Promise<Structure[]> => {
    return fetch('https://itzakupki.ru/api/backend/v1/organization_struct', {
        method: 'GET',
        credentials: 'include',
        headers: {
            'Content-Type': 'application/json',
        },
    })
        .then((response) => {
            if (response.ok) {
                return response.json()
            } else {
                throw new Error('Failed to fetch organization_struct')
            }
        })
        .catch((e) => {
            console.error(e)
        })
}

const loadRole = async (): Promise<Role> => {
    return fetch('https://itzakupki.ru/api/backend/v1/organizations/roles/', {
        method: 'GET',
        credentials: 'include',
        headers: {
            'Content-Type': 'application/json',
        },
    })
        .then((response) => {
            if (response.ok) {
                return response.json()
            } else {
                throw new Error('Failed to fetch organization')
            }
        })
        .catch((e) => {
            console.error(e)
        })
}

