import React, { useState } from 'react'
import { useNavigate, useParams, useSearchParams } from 'react-router-dom'
import { PlanPosition } from '../types'

type Props = {
    position: PlanPosition
    handleSave: () => void
}

const Footer: React.FC<Props> = ({ position, handleSave }) => {
    const navigate = useNavigate()
    const [isStatusChanging, setStatusChanging] = useState<boolean>(false)
    const [isStageChanging, setStageChanging] = useState<boolean>(false)
    const [isDeleting, setDeleting] = useState<boolean>(false)
    const [isCopying, setCopying] = useState<boolean>(false)
    const [isCreating, setCreating] = useState<boolean>(false)

    const [isAnnulating, setAnnulating] = useState<boolean>(false)
    const [annulatePlanPositionPopup, setAnnulatePlanPositionPopup] =
        useState<boolean>(false)
    const [annulateReason, setAnnulateReason] = useState('REJECT')

    const statusChangePosition = async (
        planGuid: string,
        guid: string,
        statusPos: string
    ): Promise<void> => {
        const url = `https://itzakupki.ru/api/backend/v1/plan_positions_draft_change_status/?plan_guid=${planGuid}&pos_guid=${guid}`

        try {
            const response = await fetch(url, {
                method: 'PUT',
                credentials: 'include',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({ position_status: statusPos }),
            })

            if (!response.ok) {
                throw new Error(
                    `Failed to change status position from. Status: ${response.status}`
                )
            }

            console.log('Position status change successfully')
        } catch (error) {
            console.error('Error while changing status position:', error)
            setStatusChanging(false)
        } finally {
        }
    }

    const stageChangePosition = async (
        planGuid: string,
        guid: string,
        stagePos: string
    ): Promise<void> => {
        const url = `https://itzakupki.ru/api/backend/v1/plan_positions_draft_change_stage/?plan_guid=${planGuid}&pos_guid=${guid}`

        try {
            const response = await fetch(url, {
                method: 'PUT',
                credentials: 'include',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({ position_stage: stagePos }),
            })

            if (!response.ok) {
                throw new Error(
                    `Failed to change stage position from. Status: ${response.status}`
                )
            }

            console.log('Position stage change successfully')
        } catch (error) {
            console.error('Error while changing stage position:', error)
            setStageChanging(false)
        } finally {
        }
    }

    const copyPositionDraft = async (
        planGuid: string,
        guid: string
    ): Promise<void> => {
        const url = `https://itzakupki.ru/api/backend/v1/plan_position_draft_from_draft/?plan_guid=${planGuid}&from_guid=${guid}`

        try {
            const response = await fetch(url, {
                method: 'POST',
                credentials: 'include',
                headers: {
                    'Content-Type': 'application/json',
                },
            })

            if (!response.ok) {
                throw new Error(
                    `Failed to create new position from. Status: ${response.status}`
                )
            }

            console.log('Position created successfully')
        } catch (error) {
            console.error('Error while creating position:', error)
            setCopying(false)
        } finally {
        }
    }

    const changePosition = async (
        planGuid: string,
        guid: string
    ): Promise<void> => {
        const url = `https://itzakupki.ru/api/backend/v1/plan_position_draft_from/?plan_guid=${planGuid}&from_guid=${guid}`

        try {
            const response = await fetch(url, {
                method: 'POST',
                credentials: 'include',
                headers: {
                    'Content-Type': 'application/json',
                },
            })

            if (!response.ok) {
                throw new Error(
                    `Failed to create new position from. Status: ${response.status}`
                )
            }

            if (response.ok) {
                const responseData = await response.json()

                // Обработка успешного ответа
                const newGuid = responseData.plan_pos?.guid

                if (newGuid) {
                    navigate(
                        `/hub/plans/${planGuid}/positions/draft/${newGuid}`
                    )
                }
            }

            console.log('Position created successfully')
        } catch (error) {
            console.error('Error while creating position:', error)
            setCreating(false)
        } finally {
        }
    }

    const annulatePosition = async (
        planGuid: string,
        guid: string,
        cancellationReason: string
    ): Promise<void> => {
        const url = `https://itzakupki.ru/api/backend/v1/plan_position_draft_annulate/?plan_guid=${planGuid}&guid=${guid}&cancellation_reason=${cancellationReason}`

        try {
            const response = await fetch(url, {
                method: 'POST',
                credentials: 'include',
                headers: {
                    'Content-Type': 'application/json',
                },
            })

            if (!response.ok) {
                throw new Error(
                    `Failed to annulate position from. Status: ${response.status}`
                )
            }

            console.log('Position annulate successfully')
        } catch (error) {
            console.error('Error while annulating position:', error)
            setAnnulating(false)
        } finally {
        }
    }

    const deletePosition = async (
        planGuid: string,
        guid: string
    ): Promise<void> => {
        const url = `https://itzakupki.ru/api/backend/v1/plan_position_draft/?plan_guid=${planGuid}&guid=${guid}`

        try {
            const response = await fetch(url, {
                method: 'DELETE',
                credentials: 'include',
                headers: {
                    'Content-Type': 'application/json',
                },
            })

            if (!response.ok) {
                throw new Error(
                    `Failed to delete position. Status: ${response.status}`
                )
            }

            console.log('Position deleted successfully')
        } catch (error) {
            console.error('Error while deleting position:', error)
            setDeleting(false)
        } finally {
        }
    }

    const handleStatusChange = async (
        plan_guid: string,
        pos_guid: string,
        status: string
    ) => {
        // Нажато готово
        statusChangePosition(plan_guid, pos_guid, status)
    }

    const handleStageChange = async (
        plan_guid: string,
        pos_guid: string,
        status: string
    ) => {
        // Нажато готово
        stageChangePosition(plan_guid, pos_guid, status)
    }

    const handleCopy = async (plan_guid: string, pos_guid: string) => {
        // Нажато готово
        copyPositionDraft(plan_guid, pos_guid)
        navigate(`/hub/plans/${plan_guid}/positions?stage=Редактирование`)
    }

    const handleChange = async (plan_guid: string, pos_guid: string) => {
        // Нажато готово
        changePosition(plan_guid, pos_guid)
    }

    const handleAnnulate = async (
        plan_guid: string,
        pos_guid: string,
        cancellationReason: string
    ) => {
        // Нажато готово
        annulatePosition(plan_guid, pos_guid, cancellationReason)
        navigate(`/hub/plans/${plan_guid}/positions?stage=Редактирование`)
    }

    const handleAnnulatePlanPositionPopup = () => {
        setAnnulatePlanPositionPopup(!annulatePlanPositionPopup)
    }

    const handleAnnulateReasonChange = async (value: string) => {
        setAnnulateReason(value)
    }

    const handleDelete = async (
        plan_guid: string,
        pos_guid: string,
        stage = position.position_stage
    ) => {
        // Нажато готово
        deletePosition(plan_guid, pos_guid)
        navigate(`/hub/plans/${plan_guid}/positions?stage=${stage}`)
    }

    const handlePrint = async () => {
        // Открываем окно печати
        window.print()
    }

    return (
        <footer className="flex flex-row w-full items-center justify-end">
            <div className="flex flex-row gap-2 p-2 w-full bg-zinc-800">
                {position.position_stage == 'Редактирование' ? (
                    <>
                        {/* <button className="button [&]:bg-green-700">
                            Проверить
                        </button> */}
                        <button
                            className="button"
                            onClick={() =>
                                handleStatusChange(
                                    position.plan_guid,
                                    position.guid,
                                    'Готово'
                                )
                            }
                        >
                            Готово
                        </button>
                        <button
                            className="button"
                            onClick={() =>
                                handleStageChange(
                                    position.plan_guid,
                                    position.guid,
                                    'Подготовка к размещению'
                                )
                            }
                        >
                            Направить на подготовку к размещению
                        </button>
                        <span className="grow"></span>
                        <button className="button" onClick={handleSave}>
                            Сохранить
                        </button>
                        <button
                            className="button"
                            onClick={() =>
                                handleCopy(position.plan_guid, position.guid)
                            }
                        >
                            Копировать
                        </button>
                        {/* <button
                            className="flex button min-w-20 items-center justify-center"
                            onClick={handlePrint}
                        >
                            Печать
                        </button> */}
                        <button
                            className="button"
                            onClick={() =>
                                handleDelete(
                                    position.plan_guid,
                                    position.guid,
                                    position.position_stage
                                )
                            }
                        >
                            Удалить
                        </button>
                        <button
                            className="button"
                            onClick={() => {
                                navigate(
                                    `/hub/plans/${position.plan_guid}/positions?stage=${position.position_stage}`
                                )
                            }}
                        >
                            Закрыть
                        </button>
                    </>
                ) : position.position_stage == 'Подготовка к размещению' ? (
                    <>
                        {/* <button className="button [&]:bg-green-700">
                                Проверить
                            </button> */}
                        <button
                            className="button"
                            onClick={() => {
                                handleStageChange(
                                    position.plan_guid,
                                    position.guid,
                                    'Редактирование'
                                )
                                handleStatusChange(
                                    position.plan_guid,
                                    position.guid,
                                    'Черновик'
                                )
                            }}
                        >
                            Направить на редактирование
                        </button>
                        <button
                            className="button"
                            onClick={() =>
                                handleStatusChange(
                                    position.plan_guid,
                                    position.guid,
                                    'Готово'
                                )
                            }
                        >
                            Готово
                        </button>
                        <button
                            className="button"
                            onClick={() => {
                                handleStageChange(
                                    position.plan_guid,
                                    position.guid,
                                    'Отправка в ЕИС'
                                )
                                handleStatusChange(
                                    position.plan_guid,
                                    position.guid,
                                    'Готово к отправке'
                                )
                            }}
                        >
                            Направить на отправку в ЕИС
                        </button>
                        <span className="grow"></span>
                        <button
                            className="button"
                            onClick={() =>
                                handleCopy(position.plan_guid, position.guid)
                            }
                        >
                            Копировать
                        </button>
                        {/* <button
                            className="flex button min-w-20 items-center justify-center"
                            onClick={handlePrint}
                        >
                            Печать
                        </button> */}
                        <button
                            className="button"
                            onClick={() => {
                                navigate(
                                    `/hub/plans/${position.plan_guid}/positions?stage=${position.position_stage}`
                                )
                            }}
                        >
                            Закрыть
                        </button>
                    </>
                ) : position.position_stage == 'Отправка в ЕИС' ? (
                    <>
                        {/* <button className="button [&]:bg-green-700">
                                    Проверить
                                </button> */}
                        <button
                            className="button"
                            onClick={() => {
                                handleStageChange(
                                    position.plan_guid,
                                    position.guid,
                                    'Подготовка к размещению'
                                )
                                handleStatusChange(
                                    position.plan_guid,
                                    position.guid,
                                    'Подготовка'
                                )
                            }}
                        >
                            Направить на подготовку к размещению
                        </button>
                        <span className="grow"></span>
                        <button className="button" onClick={handleSave}>
                            Сохранить
                        </button>
                        <button
                            className="button"
                            onClick={() =>
                                handleCopy(position.plan_guid, position.guid)
                            }
                        >
                            Копировать
                        </button>
                        {/* <button
                            className="flex button min-w-20 items-center justify-center"
                            onClick={handlePrint}
                        >
                            Печать
                        </button> */}
                        <button
                            className="button"
                            onClick={() => {
                                navigate(
                                    `/hub/plans/${position.plan_guid}/positions?stage=${position.position_stage}`
                                )
                            }}
                        >
                            Закрыть
                        </button>
                    </>
                ) : position.position_stage == 'План закупки в ЕИС' ? (
                    <>
                        {/* <button className="button [&]:bg-green-700">
                                        Проверить
                                    </button> */}
                        <span className="grow"></span>
                        <button
                            className="button"
                            onClick={() =>
                                handleChange(position.plan_guid, position.guid)
                            }
                        >
                            Внести изменение
                        </button>
                        <button
                            className="button"
                            onClick={() =>
                                handleCopy(position.plan_guid, position.guid)
                            }
                        >
                            Копировать
                        </button>
                        <button
                            className="button"
                            onClick={handleAnnulatePlanPositionPopup}
                        >
                            Аннулировать
                        </button>
                        {/* <button
                            className="flex button min-w-20 items-center justify-center"
                            onClick={handlePrint}
                        >
                            Печать
                        </button> */}
                        <button className="button">Скачать</button>
                        <button
                            className="button"
                            onClick={() => {
                                navigate(
                                    `/hub/plans/${position.plan_guid}/positions?stage=${position.position_stage}`
                                )
                            }}
                        >
                            Закрыть
                        </button>
                    </>
                ) : (
                    ''
                )}
            </div>
            {annulatePlanPositionPopup && (
                <div
                    className="fixed top-0 left-0 w-full h-full bg-black bg-opacity-50 flex justify-center items-center"
                    onClick={handleAnnulatePlanPositionPopup}
                >
                    <div
                        className="p-5 rounded-lg bg-[#262427] w-fit max-h-[600px]"
                        onClick={(e) => e.stopPropagation()}
                    >
                        <div className="flex flex-col text-sm text-white items-center justify-center gap-5">
                            <div className="flex flex-col">
                                <label className="flex mb-1 text-sm text-[#8A8A8A]">
                                    Обоснование аннулирования позиций
                                </label>
                                <select
                                    className="flex p-2 rounded-md border-0 text-sm bg-[#181818] text-white focus:outline-none focus:ring-1 focus:ring-[#FFFFFF]"
                                    value={annulateReason}
                                    onChange={(e) =>
                                        handleAnnulateReasonChange(
                                            e.target.value
                                        )
                                    }
                                >
                                    <option value="REJECT">
                                        Отказ от проведения закупки
                                    </option>
                                    <option value="INVALID">
                                        Признание торгов недействительными
                                    </option>
                                    <option value="NOT_PERFORMED">
                                        Торги не состоялись
                                    </option>
                                </select>
                            </div>
                            <div className="flex w-full flex-row items-center justify-end">
                                <button
                                    className="flex px-4 py-1 w-24 h-7 rounded-full shadow-md text-xs text-white ml-2 items-center justify-center bg-green-700 hover:bg-green-600"
                                    onClick={() =>
                                        handleAnnulate(
                                            position.plan_guid,
                                            position.guid,
                                            annulateReason
                                        )
                                    }
                                >
                                    Аннулировать
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            )}
        </footer>
    )
}

export default Footer
